
.scaling-svg-container {
  position: relative;
  height: 0;
  width: 100%;
  padding: 0;
  padding-bottom: 100%;
}
.scaling-svg {
  max-height: 85vh;
  margin-top: -16px;
}
.itemLabel,
.legendLabel,
.areaLabel {
  font-family: 'Oswald', sans-serif;
  opacity: 1;
  -webkit-transform-origin: top right;
          transform-origin: top right;
  -moz-transform-origin: 0 0;
}
.areaFill {
  opacity: 0.5;
}
.areaLabel {
  fill: rgba(255, 255, 255, 0.7);
}
.legendLabel {
  font-style: normal;
  fill: #333;
}
.legendArcBg {
  /*fill: red;*/
  fill: transparent;
}
.legendArcOuter {
  fill: rgba(255, 255, 255, 0.5);
}
.RadarChart .ItemLabel {
  cursor: pointer;
  fill: #333;
}
.RadarChart .ItemLabel:hover {
  font-weight: bold;
}
.RadarChart .Section--labelArc {
  fill: none;
}
.RadarChart .areaLabel {
  fill: rgba(255, 255, 255, 0.7);
}
.RadarChart.debug .Legend-background {
  stroke: magenta;
}
.RadarChart.debug .Legend-arcOuter {
  stroke: cyan;
}
.RadarChart.debug .Legend-innerPath {
  stroke: deeppink;
}
.RadarChart.debug .Section--labelArc {
  fill: none;
  stroke: red;
  stroke-dasharray: 5 5;
  stroke-width: 5;
}
.RadarChar-debug {
  stroke: red;
}
.RadarChar-debug .cyan {
    stroke: cyan;
}
.RadarChar-debug .angleStep {
    stroke: blue;
}



